@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  padding: 0;
  background-image: url(https://images.pexels.com/photos/167699/pexels-photo-167699.jpeg?cs=srgb&dl=clouds-cloudy-cold-167699.jpg&fm=jpg);
}


.app-header {
  background-color: #007bff;
  font-size: 40px;
  font-family: -apple-system,BlinkMacSystemFont,segoe ui,roboto,oxygen,cantarell,helvetica neue,ubuntu,sans-serif;
  color: white;
  text-align: center;
}

.App-link {
  color: #61dafb;
}

.searchInput {
  height: 50px;
  width: 75vw;
  padding-right: 10px;
  padding-left: 10px;
  border-color: #61dafb;
  border-radius: 2px;
  border-width: 2px;
}

.searchButton {
  height: 50px;
}

#search_container {
  position: relative;
  transition: top 0.8s;
}

.hoverfnc :hover{
  cursor: pointer;
}
